
#geolocation {
  width: 32px;
  height: 32px;
  cursor: pointer;
  background: #fff;
  opacity: 0.8;
  box-shadow: 0px 2px 4px #aaaaaa;
  border-radius: 2px;
}
.geo-icon {
  width: 26px;
  height: 26px;
  position: absolute;
  top: 3px;
  left: 3px;
}



